import React from "react";
import logo from "../img/weather-app-icon.png";
const Form = props => {
  return (
    <nav className="navbar navbar-color navbar-default navbar-fixed-top ">
      <div className="container resize">
        <a className="navbar-brand " href="#123">
          <img src={logo} className="imgSize" alt="" />
          <h5 className="brand-color">WeatherFinder</h5>
        </a>
        <form className="form-inline ml-auto" onSubmit={props.getWeather}>
          <input
            className="form-control mr-sm-2"
            type="text"
            name="city"
            required
            placeholder="City...."
          />
          <input
            className="form-control"
            type="text"
            name="country"
            required
            placeholder="Country...."
          />
          <button className="btn btn-dark ml-sm-2">GetWeather</button>
        </form>
      </div>
      <div className="container">
        <span className="ml-auto text-warning ">
          {props.weatherDesc.error && <p>{props.weatherDesc.error}</p>}
        </span>
      </div>
    </nav>
  );
};

export default Form;
